.filter {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 24px;
    margin-bottom: 40px;
};

.filter__list__tab {
    display: none;

    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;

        gap: 14px;
    }

    @media screen and (min-width: 1200px) {
        display: none;
        
    }
}

.filter__list__des {
    display: none; 

    @media screen and (min-width: 1200px) {
            display: flex;
            align-items: center;

            gap: 16px;
        }
}

.filter__item__btn {
    padding: 8px 21px;
}

.filter__btn {
    height: 31px;

    display: flex;
    align-items: center;
    justify-content: center;
    
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;

    background-color: transparent;
    border-radius: 20px;

    gap: 10px;

};

.filter__categories {
    width: 112px;

    border: 1px solid #4440F7;

    color: #4B48DB;

    @media screen and (min-width: 768px) {
            display: none;
        }
};

.description__filter__calendar {
    display: none;

    @media screen and (min-width: 1200px) {
            display: inline;

            margin: 0;

            font-family: 'Manrope';
            font-style: normal;
            font-weight: 700;
            font-size: 18px;
            line-height: 25px;

            color: #111321;
        }

}

.filter__calendar {
    width: 152px;

    border: 1px solid #111321;

    @media screen and (min-width: 768px) {
            width: 168px;
            height: 31px;

            font-size: 14px;
        }
    
}

.filter__calendar__text {
    letter-spacing: 0.02em;
    opacity: 0.4;
}

.categories__btn__post__el {
    width: 14px;
    height: 14px;

    background-color: #4440F7;
}

.calendar__btn__pre__el {
    width: 19px;
    height: 19px;

    background-color: #4440F7;
}

.calendar__btn__post__el {
    width: 9px;
    height: 15px;
    
    background-color: #A2A2A2;

    transform: rotate(-90deg);
}

.filter__item__btn {
    display: flex;
    align-items: center;
    justify-content: center;

    font-family: 'Manrope';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    
    color: #4440F7;

    background-color: transparent;
    border: 1px solid #4440F7;
    border-radius: 20px;
}

.others {
    gap: 10px;
}


.news {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.wrapper {
    @media screen and (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        
        grid-auto-rows: auto;
        grid-gap: 30px;
    }

    @media screen and (min-width: 1200px) {
        grid-template-columns: 1fr 1fr 1fr;
    
        grid-gap: 40px 32px;
    }
}

.card {
    width: 288px;
    height: 630px;

    @media screen and (min-width: 768px) {
        width: 353px;
        height: 675px;
    }

    @media screen and (min-width: 1200px) {
        width: 395px;
        height: 675px;
    }
}

.card:nth-child(2) {
    @media screen and (min-width: 1200px) {
        width: 394px;
    }
}

.card:nth-child(4) {
    @media screen and (min-width: 1200px) {
        width: 394px;
    }
}

.card:nth-child(7) {
    @media screen and (min-width: 1200px) {
        width: 394px;
    }
}

.card:not(:last-child) {
    @media screen and (max-width: 767px) {
        margin-bottom: 28px;
    }
}

.news__weather {
    width: 288px;
    height: 469px;

    margin-bottom: 28px;

    @media screen and (min-width: 768px) {
        position: absolute;
        top: 0;
        right: 0;

        width: 353px;
        height: 675px;
    }

    @media screen and (min-width: 1200px) {
        width: 394px;
    }
}

.box1 {
    @media screen and (min-width: 768px ) and (max-width: 1199px) {
    grid-column-start: 1;
    grid-column-end: 3;
    }
}

.box3 {
    @media screen and (min-width: 1200px) {
        grid-column-start: 1;
    }
}

