.header {
    position: fixed;
    z-index: 1;
    top:0;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.header-container{
    height: 81px;
    padding-right: 16px;
    padding-left: 16px;
    padding-top: 24px;
    padding-bottom: 22px;
    margin-left: auto;
    margin-right: auto;
    //position: relative;
    display: flex;
    align-items: center;
    flex-direction: row;
    min-width: 320px;

    @media screen and (min-width: 320px) {
        width: 320px;}

    @media screen and (min-width:768px) {
        width: 768px;
        height: 106px;
        padding-top: 32px;
        padding-bottom: 28px;
    }

    @media screen and (min-width:1280px) {
        width: 1280px;
        height: 113px;
        padding-top: 28px;
    }
}

.header-logo-text{
    font-family: 'Manrope';
    color: $primary-black-color;
    font-weight: 700;
    font-size: 24px;
    margin-right: 169px;

    @media screen and (min-width:768px) {
        font-size: 32px;
        margin-right: 44px;
    }

    @media screen and (min-width:1280px) {
        font-size: 42px;
        margin-right: 98px;
    }
}

// Menu container

.header-menu-container{
    display: none;

    &.is-open {
        display: block;
        position: absolute;
        z-index: 1;
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 80px;
        background-color: $background-color;
        padding-top: 45px;
    }

    @media screen and (min-width:768px){
        position: static;
        display: block;
    }
}

.header-nav {
    display: flex;
    flex-direction: column;
    gap: 30px;
    font-family: 'Manrope';
    font-weight: 500;
    font-size: 16px;
    line-height: 1.38;

    @media screen and (min-width:768px){
    flex-direction: row;
    gap: 36px;
    font-weight: 700;
    margin-right: 32px;
}

    @media screen and (min-width:1280px){
    gap: 59px;
    font-size: 18px;
    margin-right: 0px;
}
}

.header-link{
    padding: 6px 16px;
    color: inherit;
    display: flex;
    align-items: center;

    @media screen and (min-width:768px){
        display: block;
        padding: 8px 4px;
    }

    @media screen and (min-width:1280px){
        padding: 5px;
    }

}

.current {

    @media screen and (max-width:767px){
    background-color:  $primary-blue-color;
    color: $background-color;
    }

    @media screen and (min-width:768px){
    border-bottom: 1px solid $primary-blue-color;
    }
}

.icon-mob-menu-arrow {
    fill: transparent;
    margin-left: auto;

    @media screen and (min-width:768px){
    display: none;
    }
}

.icon-mob-menu{
    border: 1px solid $background-color;
    border-radius: 50%;
    margin-right: 14px;


    @media screen and (min-width:768px){
    display: none;
    }
}

// Burger buttons

.burger-btn-open, .burger-btn-close{
    height: 24px;
    width: 24px;
    padding: 0;
    border: none;
    background-color: transparent;

    @media screen and (min-width:768px){
        display: none;}
}

.burger-icon{
    fill: $primary-black-color;
}

.header-menu-container.is-open .burger-btn-open{
    display: none;
}

.burger-btn-close {
    display: none;

    .header-menu-container.is-open &{
        display: block;
    }
}    




// Search form

@keyframes scale-up-br {
  0% {
    transform: scale(0.5);
    transform-origin: 100% 100%;
  }
  100% {
    transform: scale(1);
    transform-origin: 100% 100%;
  }
}

.header-button-opensearch{
    margin-right: 15px;
    padding: 0;
    cursor: pointer;
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;

    @media screen and (min-width:768px) {
        display: none;
    }
}

.header-search-form {
    position: relative;
    display: none;
    animation: scale-up-br 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;

    @media screen and (min-width:768px) {
        display: block;
        margin-right: 24px;
        animation: none;
    }

    @media screen and (min-width: 1280px) {
        margin-right: auto;
        margin-left: auto;
    }
}

.header-search-input {
    width: 173px;
    height: 31px;
    padding: 5px 8px 5px 48px;
    background-color: transparent;
    border: 1px solid $primary-black-color;
    border-radius: 20px;
    outline: none;

    @media screen and (min-width: 768px) {
        width: 213px;
    }

    @media screen and (min-width: 1280px) {
        width: 288px;
    }
}

.header-search-input::placeholder {
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 12px;
    line-height: 1.75;
    letter-spacing: 0.02em;
    color: $primary-black-color;
    opacity: 0.4;

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.71;
    }
}

.header-button-makesearch {
    position: absolute;
    padding: 0;
    top: 6px;
    left: 12px;
    border: none;
    background-color: transparent;
}


//Theme

.theme{
    display: none;

    .header-menu-container.is-open ~ &{
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 2;
    left: 16px;
    bottom: -85vh;
    }

    @media (min-width:768px){
        display: flex;
        position: static;
    }
}


.switch{
  position: relative;
  display: block;
  width: 40px;
  height: 21px;
  margin-left: 8px;
  margin-right: 8px;

   @media (min-width:1280px){
    margin-left: 12px;
    margin-right: 12px;
    }
  
}

.switch input {
    display: none;
  } 

.slider {
    display: block;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color:$primary-white-color;
    transition: all 0.4s;
    border: 1px solid #4B48DB;
    border-radius: 20px;
  }

.slider.round:before {
    border-radius: 50%;
  }

.slider:before {
    position: absolute;
    content: '';
    height: 16px;
    width: 16px;
    left: 1px;
    top: 2px;
    background-color: #4B48DB;
    transition: all 0.4s;
  }
  
.switch input:checked + .slider {
    background-color: #4B48DB;
    border: 1px solid $primary-white-color;
  }

.switch input:checked + .slider:before {
    background-color: $primary-white-color;
    transform: translateX(20px);
  }

.switch input:focus + .slider {
    box-shadow: 0 0 1px  #4B48DB;
  }

.theme__icon {
    @media (min-width:1280px){
        display: none;
    }
  }

.theme__light, .theme__dark {
    display: none;

    @media (min-width:1280px){
        display: block;
        font-size: 18px;
    }
  }

.theme__light {
    color: #4B48DB;
}

.theme__dark {
    color:#5F6775;
}
